@import url(http://fonts.googleapis.com/css?family=Roboto:300,400,500,700);

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(../../bower_components/material-design-iconfont/iconfont/MaterialIcons-Regular.eot); /* For IE6-8 */
  src: local('Material Icons'),
       local('MaterialIcons-Regular'),
       url(../../bower_components/material-design-iconfont/iconfont/MaterialIcons-Regular.woff2) format('woff2'),
       url(../../bower_components/material-design-iconfont/iconfont/MaterialIcons-Regular.woff) format('woff'),
       url(../../bower_components/material-design-iconfont/iconfont/MaterialIcons-Regular.ttf) format('truetype');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  width: 1em;
  height: 1em;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}

main {
  padding: 20px;
  background: #fafafa;
}

.mdl-card {
  width: 100%;
}
.mdl-card__title {
  color: #fff;
  height: 176px;
  background: url('../assets/images/yeoman.png') center / cover;
  background-size: 110px;
  background-repeat: no-repeat;
}
.mdl-card__title > .mdl-card__title-text {
  color: #000;
}
.mdl-card__menu {
  color: #000;
}
.mdl-card__actions {
  text-align: center;
}

a, a:visited, a:hover{
  text-decoration: none;
}

.btn{
    font-size: 12px;
    padding: 4px 15px;
    line-height: 20px;
    font-weight: 400;
    -moz-border-radius: 0px;
    -webkit-border-radius: 0px;
    border-radius: 0px;
    -webkit-transition: all 200ms ease;
    -moz-transition: all 200ms ease;
    -ms-transition: all 200ms ease;
    -o-transition: all 200ms ease;
    transition: all 200ms ease;
}

/* LOGIN */
.login-container {
  float: left;
  width: 100%;
  min-height: 100%;
  background: url("../../../../assets/images/login_bg.png") left top repeat #1b1e24;
  height: auto;;
  position: absolute;
  top: 0;
  left: 0;
}
.login-container .login-box {
  width: 400px;
  margin: 0px auto;
  padding-top: 100px;
}
.login-container .login-box .login-logo {
  background: url("../../../../assets/images/logo.png") top center no-repeat;
  width: 100%;
  height: 50px;
  float: left;
  margin-bottom: 10px;
}
.login-container .login-box .login-body {
  width: 100%;
  float: left;
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  -moz-border-radius: 0px;
  -webkit-border-radius: 0px;
  border-radius: 0px;
}
.login-container .login-box .login-body .login-title {
  color: #FFF;
  font-size: 19px;
  font-weight: 300;
  margin-bottom: 20px;
}
.login-container .login-box .login-body .form-control {
  border: 0px;
  background: rgba(0, 0, 0, 0.2);
  padding: 10px 15px;
  color: #CCC;
  line-height: 20px;
  height: auto;
  border-radius: 4px;
  width: 90%;
  margin: 6px;
}
.login-container .login-box .login-body .btn-link {
  color: #FFF;
  border: 0px;
}

.login-container .login-box .login-body .login-submit,
.login-container .login-box .login-body .login-submit:hover,
.login-container .login-box .login-body .login-submit:visited,
.login-container .login-box .login-body .login-submit:focus {
  color: #FFF;
  border: 0px;
  margin:0px;
  padding:0px;
  background-color: #009688;
}

.login-container .login-box .login-body .form-group .login-forgot-pw:hover{
  color: #009688;
  border: 0px;
}

.login-container .login-box .login-body .login-forgot-pw{
  color: #FFF;
  padding: 7px;
}

.login-container .login-box .login-footer {
  width: 100%;
  /*float: left;*/
  padding: 10px;
  color: #FFF;
}

.login-container .login-box .login-footer .text-center {
  font-size: 10px;
  color: #FFF;
  padding: 10px;
  margin: 0 auto;
  text-align: center;
}

.login-container .login-btn-grp{
  width: 50%;
}
/* END LOGIN */

