.drawer-links {
  -webkit-font-smoothing: antialiased;
  color: rgb(120, 144, 156);
  font-size: 13px;
  font-weight: bold;
  cursor: hover;
  margin-bottom: 20px;

}

.demo-avatar {
  width: 48px;
  /*height: 48px;*/
  border-radius: 24px;
}

.acme-malarkey {
  text-transform: capitalize;
  color: #cb3837;
}

.acme-malarkey:after {
  animation: cursor-blink 0.4s linear infinite;
  content: "|";
  color: #cb3837;
}

@keyframes cursor-blink {
  1% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

/* ================================================== */
/* HEADER
/* ================================================== */
	header {
		width: 100%;
		/*height: 130px;*/
		overflow: hidden;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 999;
		-webkit-transition: height 0.3s;
		-moz-transition: height 0.3s;
		-ms-transition: height 0.3s;
		-o-transition: height 0.3s;
		transition: height 0.3s;
		background:rgba(0,0,0,0);
		font-family: "Open Sans",Arial, Helvetica, sans-serif;
		font-size: 13px;
		font-weight:400;
		color: #707070;
		line-height: 1.8em;
		padding: 0;
	}
	header.header-bg{
		background:rgba(0,0,0,0.5);
	}
	header h1#logo {
		display: inline-block;
		height: 100%;
		line-height: 120px;
		float: left;
		font-family: "Oswald", sans-serif;
		font-size: 60px;
		color: white;
		font-weight: 400;
		-webkit-transition: all 0.3s;
		-moz-transition: all 0.3s;
		-ms-transition: all 0.3s;
		-o-transition: all 0.3s;
		transition: all 0.3s;
	}
	header h1#logo .logo-2{
		display:none;
	}
	header nav {
		float:right;
	}
	header nav a {
		-webkit-transition: all 0.3s;
		-moz-transition: all 0.3s;
		-ms-transition: all 0.3s;
		-o-transition: all 0.3s;
		transition: all 0.3s;
	}
	header nav a:hover {
		color: #555;
	}
	header.smaller #mainmenu a{
		padding: 18px 20px 18px 20px !important;
		color:#333;
	}
	header.smaller {
		height: 70px;
		background:#fff;
		border-bottom:solid 1px #ddd;
	}
	h1#logo {
		font-size:26px;
		margin:0;
		text-align:center;
		letter-spacing:5px;
		text-transform:uppercase;
		font-weight:400;
		color: #000;
		font-family: 'Raleway',Arial, Helvetica, sans-serif;
		font-weight:400;
	}
	.logo-smaller h1#logo {
		width: 150px;
		height:30px;
		line-height: 65px;
		font-size: 30px;
	}
	.logo-smaller h1#logo img{
		width:auto;
		height:30px;
		line-height: 75px;
		font-size: 30px;
	}
	.logo-smaller  h1#logo .logo-1{
		display:none;
	}
	.logo-smaller  h1#logo .logo-2{
		display:inline-block;
	}
	header.fixed #mainmenu a{
		padding: 18px 20px 18px 20px !important;
		color:#333;
	}
	header.fixed {
		height: 70px;
		background:rgba(255,255,255,0.90);
		border-bottom:solid 1px #ddd;
	}
	header.fixed h1#logo {
		width: 150px;
		height:30px;
		line-height: 65px;
		font-size: 30px;
	}
	header.fixed h1#logo img{
		width:auto;
		height:30px;
		line-height: 75px;
		font-size: 30px;
	}
	header.fixed h1#logo img.logo-1{
		display:none;
	}
	header.fixed h1#logo img.logo-2{
		display:inline-block;
	}


/* ================================================== */
/* HEADER TRANSITION
/* ================================================== */
	header, header.smaller {
		-o-transition:.5s;
		-ms-transition:.5s;
		-moz-transition:.5s;
		-webkit-transition:.5s;
		transition:.5s;
		 outline: none;
	}

	header a,
	header a:hover {
		-o-transition: .3s;
		-ms-transition: .3s;
		-moz-transition: .3s;
		-webkit-transition: .3s;
		transition: .3s;
	}


/* ================================================== */
/* navigation */
/* ================================================== */
	#mainmenu {
		font-family:"Cabin", Arial, Helvetica, sans-serif;
		float: right;
		height: 60px;
		font-size:12px;
		letter-spacing:2px;
		font-weight:300;
		text-transform:uppercase;
	}

	#mainmenu ul {
		float: right;
		margin: 0px 0px;
		padding: 0px 0px;
		height: 30px;
	}

	#mainmenu li {
		margin: 0px 0px;
		padding: 0px 0px;
		float: left;
		display: inline;
		list-style: none;
		position: relative;
	}

	#mainmenu a {
		display: block;
		padding: 50px 20px 50px 20px;
		line-height: 35px;
		text-decoration: none;
		color:#c00;
		text-align:center;
		outline:none;
	}

	#mainmenu li li {
		font-size: 12px;
		font-weight: normal;
		border-bottom: solid 1px #333;
	}

	#mainmenu li li:last-child {
		border-bottom: none;
	}

	#mainmenu li li a {
		padding: 2px 15px 2px 15px;
		background: #222327;
		border-top: none;
		color: #ccc;
		width:170px;
		border-left:none;
		text-align:left;
	}

	#mainmenu li li li a {
		padding: 2px 15px 2px 15px;
		background: #222327;
	}

	#mainmenu li li a:hover {
		color:#eceff3;
		background: #1a8b49;
	}

	#mainmenu li ul {
		width: 172px;
		height: auto;
		position: absolute;
		top: 100%;
		left: 0px;
		z-index: 10;
		display: none;
		border:solid 1px #404040;
	}

	#mainmenu li li {
		display: block;
		float: none;
	}

	#mainmenu li:hover > ul {
		display: block;
	}

	#mainmenu li ul ul {
		left: 100%;
		top: 0px;
	}

	#mainmenu li:hover > ul {
		color: #1a8b49;
	}

	#mainmenu select {
		padding: 10px;
		height: 36px;
		font-size: 14px;
		border: none;
		background: #ff4200;
		color:#eceff3;
	}

	#mainmenu select option {
		padding: 10px;
	}

	/* menu toggle for mobile */
	#menu-btn{
		display:none;
		float:right;
		margin-top:5px;
		width:36px;
		height:36px;
		background:#333;
		padding:6px;
		text-align:center;
		cursor:pointer;
	}

	#menu-btn:before{
		font-family:FontAwesome;
		content: "\f0c9";
		font-size:24px;
		color:#eceff3;
	}

	#menu-btn:hover{
		background:#fff;
	}

	#menu-btn:hover:before{
		color:#222;
	}

/* ================================================== */
/* R E S P O N S I V E */
/* ================================================== */
	@media(min-width:980px) and (max-width: 1199px) {

		header #mainmenu a, header.smaller #mainmenu a{
			padding-left:8px !important;
			padding-right:8px !important;
		}

	}

	@media only screen and (max-width: 992px) {

	#menu-btn{
		display:block;
		position:relative;
		float:right;
		top:30px;
	}

	header{
		display:none;
	}

	header.header-bg{
		background:rgba(0,0,0,1);
	}


	header,header.fixed {
		display:block;
		position:inherit;
		margin:0;
		padding:0;
		margin-top:0;
		height:auto;
		background:#000000;
	}

	header.fixed {
		display:block;
		position:inherit;
		margin:0;
		padding:0;
		margin-top:0;
		height:auto;
		background:#000000;
	}
	header.fixed h1#logo {
		height: 100%;
		line-height: 120px;
		float: left;
	}

	header.fixed h1#logo img{
		height:auto;
	}


	h1#logo{
		margin-top:-15px;
	}
	#logo, #logo .inner {
		vertical-align:middle;
		height:auto;
	}

	#mainmenu{
		display:none;
		float:none;
		z-index:200;
		width:100%;
		margin:0;
		padding:0;
	}

	#mainmenu a{
		text-align:left;
		padding-top:10px !important;
		padding-bottom:10px !important;
	}

	#mainmenu a:hover{
		background:#111 !important;
	}


	#mainmenu li ul{
		display:block;
		position:inherit;
		margin:0;
		width:100%;
	}

	#mainmenu li{
		border-bottom:solid 1px #333;
		margin:0;
		width:100%;
		display:block;
		letter-spacing:3px;
	}

	#mainmenu li:last-child{
		margin-bottom:30px;
	}

	#mainmenu li ul{
		border-top:solid 1px #333;
	}

	#mainmenu li ul a{
		width:100%;
		background:none;
		border:none;
	}

	#mainmenu li ul li{
		border:none;
		padding-left: 40px !important;
	}

	#mainmenu li ul li a{
		display:block;
		border-left:solid 1px #333;
		border-bottom:solid 1px #333;
	}

	#mainmenu li ul li:last-child{
		border-bottom:none;
		margin:0;
	}

	#mainmenu li ul li:last-child a{
		border-bottom:none;
	}
	#mainmenu li a{
		color:#eceff3;
	}

	#content_1 {
		position:relative;
	    margin-top: 624px;
		height: 100%;
	}

	}


